import { AppBar, Avatar, Toolbar, Typography, Box, InputBase, Badge, Menu, MenuItem, styled } from '@mui/material';
import { Accessibility, Mail, Notifications } from '@mui/icons-material/';
import React, { useState } from 'react'

const StyledToolbar = styled(Toolbar)({
    display: 'flex',
    justifyContent: 'space-between',
});

const Search = styled('div')(({ theme }) => ({
    backgroundColor: 'white',
    padding: '0 10px',
    borderRadius: theme.shape.borderRadius,
    width: '40%'

}));
const Icons = styled(Box)(({ theme }) => ({
    display: 'none',
    gap: '20px',
    alignItems: 'center',
    [theme.breakpoints.up('sm')]: {
        display: 'flex'
    }
}));
const UserBox = styled(Box)(({ theme }) => ({
    display: 'flex',
    gap: '10px',
    alignItems: 'center',
    [theme.breakpoints.up('sm')]: {
        display: 'none'
    }
}));


const Navbar = () => {
    const [openProfileMenu, setOpenProfileMenu] = useState(false);
    return (
        <AppBar position='sticky'>
            <StyledToolbar>
                <Typography variant='h6' sx={{ display: { xs: 'none', sm: 'block' } }}>MATERIAL APP </Typography>
                <Accessibility sx={{ display: { xs: 'block', sm: 'none' } }} />
                <Search>
                    <InputBase placeholder='Search..' />
                </Search>
                <Icons>
                    <Badge badgeContent={4} color='error'>
                        <Mail color='white' />
                    </Badge>
                    <Badge badgeContent={4} color='error'>
                        <Notifications color='white' />
                    </Badge>
                    <Avatar
                        sx={{
                            width: 30,
                            height: 30,
                        }}
                        alt='Remy Sharp'
                        src='https://material-ui.com/static/images/avatar/1.jpg'
                        onClick={ () => setOpenProfileMenu(true) }
                        />
                </Icons>
                <UserBox>
                    <Avatar
                        sx={{
                            width: 30,
                            height: 30,
                        }}
                        alt='Remy Sharp'
                        src='https://material-ui.com/static/images/avatar/1.jpg'
                        onClick={ () => setOpenProfileMenu(true) }
                    />
                    <Typography variant='span'>Remy</Typography>
                </UserBox>
            </StyledToolbar>
            <Menu
                id="demo-positioned-menu"
                aria-labelledby="demo-positioned-button"
                open={openProfileMenu}
                onClose={ () => setOpenProfileMenu(false) }
                anchorOrigin={{
                    vertical: 'top',
                    horizontal: 'right',
                }}
                transformOrigin={{
                    vertical: 'top',
                    horizontal: 'left',
                }}
            >
                <MenuItem onClick=''>Profile</MenuItem>
                <MenuItem onClick=''>My account</MenuItem>
                <MenuItem onClick=''>Logout</MenuItem>
            </Menu>
        </AppBar>
    )
}

export default Navbar;